
import Vue from 'vue';

export default Vue.extend({
  data() {
    return {
      menuBar: false,
    };
  },
  computed: {
    openMenuBar() {
      return this.menuBar ? `display: block` : `display: none`;
    },
  },
  methods: {
    onClickMenuButton() {
      this.menuBar = true;
    },
    onclickLinkButton() {
      this.menuBar = false;
    },
  },
});
